import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import ExcelToSql from '@/components/ExcelToSql'
import SqlToExcel from '@/components/SqlToExcel'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/ExcelToSql',
      name: 'ExcelToSql',
      component: ExcelToSql
    },
    {
      path: '/SqlToExcel',
      name: 'SqlToExcel',
      component: SqlToExcel
    }
  ]
})



